@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css?family=Esteban&display=swap);
@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  min-width:100%;
  background-color: white;
  scroll-behavior: smooth;
}

body {
  min-width:100%;
  color:black;
  font-family: 'Inter';
  font-feature-settings: 'cv11' 1
}

/*Fix for the blue onclick in chrome mobile*/
input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.layout {

}
html, body {
    height: 100%;
}

img {
    display: block;
    max-width: 100%;
}

.title {
    font-family: 'Inter';
    font-size: 1.5rem;
    font-weight: 800;
}

.wrapper {
    margin: 0px 3%;
}

.arrow {
    position: absolute;
    right: 2.5rem;
    bottom: 1rem;
    -webkit-animation: bounce 0.5s infinite alternate;
            animation: bounce 0.5s infinite alternate;
    z-index: -1;
}

.subtitle {
    color: #000066;
    font-family: 'Inter';
    font-size: 1.2rem;
    font-weight: light;
}

#home-cont {
    color: white;
    background-color: #306AD0;
}

.home-intro {
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    min-height: 100vh;

}

.text {
    font-family: 'Roboto','Liberation Sans', Helvetica, sans-serif;
}



.home-titledesc {
    margin-top: 5%;
    text-align: center;
}

.buttons{
    padding-top: 15px;
}

@-webkit-keyframes bounce {
    from {
      transform: translateY(0px);
    }
    to {
      transform: translateY(-15px);
    }
  }

@keyframes bounce {
    from {
      transform: translateY(0px);
    }
    to {
      transform: translateY(-15px);
    }
  }


/*Styles for desktop*/
@media (min-width: 792px) {

    .buttons{
        display: flex;
    }

    .title {
        font-size: 2rem;
        white-space:nowrap;
    }

    .text{
        text-align: left;
        font-size: 1em;
    }

    

.home-titledesc {
    margin-top: 0px;
    width: 50em;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: flex-end;
    
}


.buttonContainer{
    display: block;
}

.home-intro {
    padding: 0px 10em;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-evenly;
    align-items: center;
    height: 100vh;

    
}

.start-cover {
    height: 40em;
    max-width: 50%;
    /*justify-content: flex-end;*/
}

.arrow {
    right: 4.5rem;
    bottom: 3rem;
}

.buttons{
    
    white-space: nowrap
}

}

